.App {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  color: #212529;
}

@media (min-width: 576px) {
  .App {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .App {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .App {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .App {
    max-width: 1140px;
  }
}


.App nav {
  padding: 0;
  background-color: #cff5ed;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .14);
}

.App nav ul {
  list-style: none;
  margin: 0;
  padding: 0;

  display: flex;
  flex-direction: row;
  justify-content: center;
}

.App nav ul li {
  display: block;
}

.App nav ul li a {
  display: block;
  padding: 1rem;
  color: #1abc9c;
  text-decoration: none;
}

.App nav ul li a:hover {
  background-color: #cbfdf3;
}

.App nav ul li a.active {
  background-color: #5dd2ba;
  color: #fff;
}